import common from '../../common/common.js'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			userid:'',
			orderList:[],
			type:0,				//类别，0:买菜，1:拼团
			page:0,
			isall:0,
			info:{
				delta:1,
			},
			
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			superTitleHeight:0,
		};
	},
	onLoad(e) {
		var that = this
		var userid = uni.getStorageSync('userid');
		that.userid = userid
		
		var id = e.id
		var act = e.act
		var otype = e.otype
		var t_type = e.type
		if(t_type == 1){
			that.type = t_type
			that.resetAttr();
			that.getUserSuperOrders();
		}else{
			that.getUserOrders();
		}
		 
		if( id != '' && act == 'payfee'){
			if(otype == 'super'){
				uni.navigateTo({
					url: '/pages/orders/superinfo?id='+id+'&act='+act,
					success: res => {
						
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.navigateTo({
					url: '/pages/orders/orderinfo?id='+id+'&act='+act,
					success: res => {
						
					},
					fail: () => {},
					complete: () => {}
				});
			}				
		}
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		that.superTitleHeight = common.titleHeight + 50;



		
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');	
		var userid = uni.getStorageSync('userid');
		that.userid = userid
		that.resetAttr();
		if(that.type == 1){
			that.getUserSuperOrders();
		}else{
			that.getUserOrders();
		}
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onReachBottom(){
		var that = this
		that.loadmore();
	},
	methods:{		
		//切换
		changeTab(e){
			var that = this
			var type = e.currentTarget.dataset.type
			console.log(type);
			if(type != that.type){
				that.resetAttr();
				if(type == 1){
					that.getUserSuperOrders();
				}else{
					that.getUserOrders();
				}
				that.type = type
			}
		},
		//重置参数
		resetAttr(){
			var that = this
			that.page = 0;
			that.isall = 0;
			that.orderList = []
		},
		//删除订单功能
		delOrderInfo(e){
			var that = this
			var order_no = e.currentTarget.dataset.orderno
			uni.showModal({
				title:'提示',
				content:'确定要删除吗？',
				success: function (res) {
					if (res.confirm) {
						console.log(order_no)
						if(that.type == 1){
							var url = common.apiUrl + 'orders/delSuperOrderInfo'
						}else{
							var url = common.apiUrl + 'orders/delOrderInfo'
						}
						uni.request({
							url: url,
							method: 'GET',
							data: {
								order_no : order_no
							},
							success: res => {
								console.log(res);
								uni.showToast({
									title:'删除成功',
									icon:'none'
								})
								that.resetAttr();
								if(that.type == 1){
									that.getUserSuperOrders();
								}else{
									that.getUserOrders();
								}
							},
							fail: () => {},
							complete: () => {}
						});
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		},
		//取消订单,货到付款订单
		cancelOrderInfo(e){
			var that = this
			var orderno = e.currentTarget.dataset.orderno
			uni.showModal({
				title:'提示',
				content:'确定要取消订单吗？',
				success: function (res) {
					if (res.confirm) {
						console.log(orderno)
						var url = common.apiUrl + 'orders/cancelOrderInfo'
						uni.request({
							url: url,
							method: 'GET',
							data: {
								orderno : orderno
							},
							success: res => {
								console.log(res);
								uni.showToast({
									title:'取消成功',
									icon:'none'
								})
								that.resetAttr();
								that.getUserOrders();
							},
							fail: () => {},
							complete: () => {}
						});
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		},
		//获取用户订单列表
		getUserOrders(){
			var that = this
			var userid = uni.getStorageSync('userid');
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			uni.request({
				url: common.apiUrl+'orders/getUserOrders',
				method: 'GET',
				data: {
					userid:userid,
					page:that.page+1
				},
				success: res => {
					console.log(res);
					that.orderList = that.orderList.concat(res.data.order_list);
					that.isall = res.data.isall;
					that.page = res.data.page
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取用户订单列表
		getUserSuperOrders(){
			var that = this
			var userid = uni.getStorageSync('userid');
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			uni.request({
				url: common.apiUrl+'orders/getUserSuperOrders',
				method: 'GET',
				data: {
					userid:userid,
					page:that.page+1
				},
				success: res => {
					console.log(res);
					that.orderList = that.orderList.concat(res.data.order_list);
					that.isall = res.data.isall;
					that.page = res.data.page
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//加载更多
		loadmore(e){
			var that = this
			var loadtype = that.type
			that.getUserOrders();
		},
		
		//订单详情
		orderInfo(e){
			var that = this
			var id = e.currentTarget.dataset.id
			var type = that.type
			if(type == 1){
				uni.navigateTo({
					url: '/pages/orders/superinfo?id='+id,
					success: res => {
						
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.navigateTo({
					url: '/pages/orders/orderinfo?id='+id,
					success: res => {
						
					},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
}
